//  
const INIT = 'message/INIT';
const START = 'message/START';
const COM = 'message/COM';
const USER = 'message/USER';
const ERROR = 'message/ERROR';
const SUCCESS2 = 'message/SUCCESS2';
const SUCCESS3 = 'message/SUCCESS3';
const FAIL2 = 'message/FAIL2';
const FAIL3 = 'message/FAIL3';
// 
// export const init = () => ({
//     type: INIT,
//     text:'     Start  ' 
//     });
export const start = () => ({
    type: START, 
    text:'com  .', 
});
export const com = () => ({
    type: COM, 
    text: 'com  .'
});
export const user = () => ({
    type: USER, 
    text:'  ' 
});
export const error = () => ({
    type: ERROR, 
    text:'   !'  
});
export const success2 = () => ({
    type: SUCCESS2,
    text: '2 !!'
});
export const success3 = () => ({
    type: SUCCESS3,
    text: '3 !!!!'
});
export const fail2 = () => ({
    type: FAIL2,
    text: '2 ...'
});
export const fail3 = () => ({
    type: FAIL3,
    text: '3 ...'
});

// 

const initialState = {
    text: '     Start  ' 
};


//  

function setMessage(state=initialState, action){
    switch(action.type) {
        case START: 
            return {text : action.text};
        case COM: 
            return {text : action.text};
        case USER: 
            return {text : action.text};
        case ERROR: 
            return {text : action.text};
        case SUCCESS2: 
        return {text : action.text};
        case SUCCESS3: 
        return {text : action.text};
        case FAIL2: 
        return {text : action.text};
        case FAIL3: 
        return {text : action.text};
        default:
            return state;
    }
};

export default setMessage;